<template>
  <v-container fluid>
    <v-row no-gutters>
      <v-col cols="12">
        <h1 class="black--text font-weight-light"></h1>
      </v-col>
      <v-col cols="4">
        <v-text-field
          label="Input Serial Number"
          autosize
          autofocus
          outlined
          clearable
          v-model="sernum"
          v-on:keyup.enter="getTestData()"
        >
        </v-text-field>
      </v-col>
      <v-col cols="4">
        <v-btn style="margin-left: 10px; margin-top: 5px;"
        large 
        color="primary"
        @click="getTestData()"
        >
        Search</v-btn>
      </v-col>
      <v-col cols="4">
        <v-menu
          ref="menu"
          v-model="menu"
          :close-on-content-click="false"
          :return-value.sync="dates"
          transition="scale-transition"
          offset-y
          min-width="auto"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="dates"
              label="Picker Date Range"
              prepend-icon="mdi-calendar"
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="dates"
            range
          >
            <v-spacer></v-spacer>
            <v-btn
              text
              color="primary"
              @click="menu = false"
            >
              Cancel
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="$refs.menu.save(dates)"
            >
              OK
            </v-btn>
          </v-date-picker>
        </v-menu>
      </v-col>
      <v-col cols="12">
        <v-data-table
          :headers="headers"
          :items="desserts"
          item-key="name"
          class="elevation-1"
          :search="search"
          :loading="loading"
          loading-text="Loading... Please wait"
        >
          <template v-slot:item.actions="{ item }">
            <v-icon
              @click="getLogList(item)"
            >
               mdi-arrow-down-bold
            </v-icon>
          </template>
        </v-data-table>
        <v-dialog
          max-width="800"
          v-model="dialog"
          persistent
        >
          <v-card>
            <v-card-title></v-card-title>
            <v-card-text>
              <h1 class="black--text font-weight-light">Test Log List as following: </h1>
            </v-card-text>
              <v-list>

                  <v-list-item
                    v-for="(item, i) in logList"
                    :key="i"
                  >
                    <v-list-item-icon>
                      <v-icon>mdi-arrow-down-bold</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <a :href="item.url" target="__blank">{{ item.name + ' (' + item.size + ')' }}</a>
                    </v-list-item-content>
                  </v-list-item>
              </v-list>
            <v-card-actions class="justify-end">
              <v-btn
                color="primary"
                @click="dialog = false"
              >Close</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { KunlunApi } from '../api'
import store from '../store'

export default {
  name: 'TestData',
  components: {
  },
  data: () => ({
    dialog: false,
    menu: false,
    dates: [],
    sernum: '',
    loading: false,
    search: '',
    headers: [
      { text: 'RECORD TIME', align: 'center', sortable: false, value: 'rectime' },
      { text: 'SERIAL', align: 'center', sortable: false, value: 'sernum' },
      { text: 'UUTTYPE', align: 'center', sortable: false, value: 'uuttype' },
      { text: 'AREA', align: 'center', sortable: false, value: 'area' },
      { text: 'RESULT', align: 'center', sortable: false, value: 'result' },
      { text: 'TEST TIME', align: 'center', sortable: false, value: 'tottime' },
      { text: 'FAIL NAME', align: 'center', sortable: false, value: 'test' },
      { text: 'MODE', align: 'center', sortable: false, value: 'mode' },
      { text: 'MACHINE', align: 'center', sortable: false, value: 'machine' },
      { text: 'CONTAINER', align: 'center', sortable: false, value: 'container' },
      { text: 'ACTION', align: 'center', sortable: false, value: 'actions' }
    ],
    desserts: [],
    logList: []
  }),
  mounted () {
    this.dates = [this.getNextDate(-7), new Date().toISOString().substr(0, 10)]
  },
  beforeDestroy () {
  },
  methods: {
    getTestData () {
      if (!this.sernum.trim()) return false
      this.loading = true
      KunlunApi({ service: 'get-test-data', sernum: this.sernum, start: this.dates[0], end: this.dates[1] })
        .then(response => {
          this.desserts = response.data.data
          store.commit('setMessage', response.data.message)
          this.loading = false
        })
        .catch(e => {
          console.log(e)
        })
    },
    getLogList (item) {
      this.dialog = true
      KunlunApi({ service: 'get-log-list', item: item })
        .then(response => {
          this.logList = response.data.data
        })
        .catch(e => {
          console.log(e)
        })
    },
    getNextDate(day) {  
      var dd = new Date()
      dd.setDate(dd.getDate() + day)
      var y = dd.getFullYear()
      var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1
      var d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate()
      return y + '-' + m + '-' + d
    }
  }
}
</script>
<style scoped>

</style>